                  <div id="" class="bm-menu-wrap" :style="sidemenuShowing ? 'position: fixed; right: 0px; z-index: 1100; width: 300px; height: 100%; transition: all 0.5s ease 0s;' : 'position: fixed; right: 0px; z-index: 1100; width: 300px; height: 100%; transition: all 0.5s ease 0s; transform: translate3d(100%, 0px, 0px);'">
                     <div class="bm-menu onboarding-sidebar__menu" style="height: 100%; box-sizing: border-box; overflow: auto;">
                        <nav class="bm-item-list" style="height: 100%;">
                           <div class="bm-item onboarding-sidebar__logo-holder" tabindex="-1" style="display: block;"><img class="onboarding-sidebar__logo" src="https://static.videoland.com/static/76623/images/vl-logo-black.svg" alt="Videoland logo"></div>
                           <div class="bm-item onboarding-sidebar__holder" tabindex="-1" style="display: block;">
                              <h2 class="onboarding-sidebar__title">Hulp nodig?</h2>
