                              <ul class="help-list">
                                 <li class="help-list__list-item"><img class="help-list__list-icon" src="https://static.videoland.com/static/76623/images/onboarding/facebook-icon.svg"><a target="_blank" id="facebook" class="help-list__channel-text" href="https://nl-nl.facebook.com/Videoland/">Facebook</a><span class="help-list__time">(10:00 - 22:00)</span></li>
                                 <li class="help-list__list-item"><img class="help-list__list-icon" src="https://static.videoland.com/static/76623/images/onboarding/twitter-icon.svg"><a target="_blank" id="twitter" class="help-list__channel-text" href="https://twitter.com/videoland">Twitter </a><span class="help-list__time">(10:00 - 22:00)</span></li>
                                 <li class="help-list__list-item"><img class="help-list__list-icon" src="https://static.videoland.com/static/76623/images/onboarding/phone-icon.svg"><a target="_blank" id="telefoon" class="help-list__channel-text" href="tel:0881232456">088 - 123 24 56</a><span class="help-list__time">(12:00 - 18:00)</span></li>
