                  <div class="onboarding__form__input onboarding-field-input" :class="showError && emailError ? 'onboarding-field-input error' : ''">
                    <label class="onboarding-field-input__label">E-mailadres</label>
                    <input name="email" type="email" autocorrect="off" autocapitalize="off" spellcheck="false" data-test="email-input" class="onboarding-field-input__field onboarding-field-input__field--email"  :class="showError && emailError ? 'onboarding-field-input__field--email error' : ''" v-model="emailaddress" @blur="blurEmailInput">
                    <div class="onboarding-error onboarding-error--error" v-if="showError && emailError"><div class="onboarding-error__icon">!</div><div class="onboarding-error__message" data-test="email-input-error">Hmm. Dat lijkt niet op een e-mailadres.</div></div>
                  </div>
                  <div class="onboarding__form__input onboarding-field-input">
                    <label class="onboarding-field-input__label">Wachtwoord</label>
                    <input name="password" :type="showPassword ? 'text' : 'password'" autocorrect="off" autocapitalize="off" spellcheck="false" data-test="password-input" class="onboarding-field-input__field onboarding-field-input__field--password" v-model="password"><a class="onboarding-password-toggle" @click.self="showPassword = !showPassword">{{ showPassword ? 'Verbergen' : 'Weergeven' }}</a>
                  </div>
                  <div class="onboarding__options onboarding__options--forgot-password"><a data-test="password-forgotten-link" class="onboarding__link" href="//www.videoland.com/wachtwoord-vergeten">Wachtwoord vergeten?</a></div>
                  <button type="submit" class="onboarding-button" :class="isValidEmail && password.length > 0 ? '' : 'onboarding-login-email__button onboarding-button--disabled'" data-test="login-button"><span>Inloggen</span></button>
                  <div class="onboarding-nav-link">Heb je nog geen account? <a class="onboarding__link accent" href="//www.videoland.com/aanmelden/email">Meld je aan</a></div>
